import React from 'react';
import { makeStyles } from '@material-ui/core/styles'
import { Grid, Paper , List, ListSubheader, ListItem, ListItemIcon, ListItemText, Collapse, Typography } from '@material-ui/core';
import NewExpertForm from './NewExpertForm';
import SingleExpert from './SingleExpert';

import { connect } from 'react-redux';

const ExpertsTab = ( { data, cb, tglBtn }) => {

  const expertList = () => {
      return data.map(obj => {
          return(
            <Grid item xs={6}>
                <SingleExpert name={obj.expertName} ranking={obj.ranking} crs={obj.crs}/>
            </Grid>
          )
      })
  }

  return (
    <Grid container>
      {expertList()}
      <NewExpertForm tglBtn={tglBtn} cb={cb}/>
    </Grid>
    
  );
}

const mapStateToProps = state => {
    return {
        data: state.expertsData
    }
}

export default connect(mapStateToProps)(ExpertsTab);