import React, {useState} from 'react';
import { Button, TextField, Grid, CssBaseline } from '@material-ui/core';
import { updateExpertName } from '../actions';
import { connect } from 'react-redux';

const NewExpertForm = ( { cb, dataSize, updateExpertName, tglBtn } ) => {
    const dataState = dataSize === 0 ? '' : `EKSPERT ${dataSize+1}`;
    const [name, setName] = useState(dataState);

    const handleNewExpert = () => {
        cb();
        if(name === ''){
        }
        else{
            updateExpertName(name);
        }
    }

    const handleTextChange = (e) => {
        setName(e.target.value);
    }

    return(
        <Grid item xs={12}>
            <Grid container justify='center' style={{padding: '10px'}}>
                <TextField onChange={handleTextChange} id="filled-basic" label="ID Decydenta" variant="filled" value={name}/>
                <Button variant="contained" color="primary" size="large" onClick={handleNewExpert}>
                    Nowy ekspert
                </Button>
            </Grid>
        </Grid>
    )
}

const mapStateToProps = (state) => {
    return {
        dataSize: state.expertsData.length
    }
}

export default connect(mapStateToProps, { updateExpertName })(NewExpertForm);