import React, { useState, useEffect } from 'react';
import { Grid, Paper, Typography, Button } from '@material-ui/core';
import {
    BarChart, AreaChart, ReferenceLine, Area, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer
  } from 'recharts';
import { connect } from 'react-redux';
import { FAHP } from 'fahpbeta';
import { scaleArrayToAhpValues } from '../ahpFunctions'
import FileSaver from 'file-saver';
import { addExpertData, resetCriteriaSliders, resetVariantSliders } from '../actions';
import './App.css';

const Ranking = ( props ) => {
    const [ ahpInstance, setAhpInstance ] = useState(undefined);
    const [ data, setData ] = useState(undefined);
    const [ rank, setRanking ] = useState(undefined);

    useEffect(() => {
        console.log(props);
        let temp = [];
        const user = new FAHP(props.variants, props.criterias);
        const critArgs = [];
        const varArgs = [];
        const comparisonsAmount = (props.variants.length**2 - props.variants.length)/2

        props.criteriasSliders.map(slider => {
            critArgs.push(slider.value);
        })

        for(let i=0; i<props.variantsSliders.length; i++){
            temp.push(props.variantsSliders[i].value);
            if(temp.length%comparisonsAmount === 0){
                varArgs.push(temp);
                temp = [];
            }
        }

        user.makePairwiseComparisons(critArgs);
        for(const criteria of props.criterias){
                user.makePairwiseComparisons(varArgs.shift(), criteria);
        }

        user.weightsAndCR();
        const metadata = user.ranking();
        setData(metadata);
        setAhpInstance(user);
        mapObj(metadata.rank);
    }, [])

    const mapObj = obj => {
        const entries = Object.entries(obj);
        const rankData = [];

        for(const entry of entries){
            rankData.push({
                "Nazwa": entry[0],
                "Waga wariantu": entry[1]
            })
        }

        setRanking(rankData);
    }

    const RankComponent = () => {
        return rank.map(item => {
            return(
                <Typography key={item["Nazwa"]}>
                    <b>{item["Nazwa"]}</b>: {item["Waga wariantu"]}
                </Typography>
            )
        })
    }

    const exportJSON = () => {
        const blob = new Blob([JSON.stringify(data, null, 2)], {type : 'application/json'});
        FileSaver.saveAs(blob, `${props.currentExpertName.replace(/ /g,'').toLowerCase()}-ahpdata.json`);
    }

    const crsVar = () => {
        let i = 0;

        return data.criterias.map(criteria => {
            return (
                <li key={criteria}><b>alternatyw w oparciu o {criteria}</b>: {data.alternativesChoicesCrs[i]} <Typography variant="caption">({`${(Math.round(data.alternativesChoicesCrs[i++]*1000)/1000)*100}%`})</Typography></li>
            )
        })
    }

    const ConsistencyRatiosData = () => {
        return(
            <Grid>
                <Grid item xs={12} style={{marginLeft: '20%', marginTop: '10px'}}>               
                    <ul>
                        <Typography variant="h5" style={{marginBottom: '5px'}}>
                            Określając wagę:
                        </Typography>
                        <li><b>kryteriów</b>: {data.criteriasChoiceCr} <Typography variant="caption">({`${(Math.round(data.criteriasChoiceCr*1000)/1000) * 100}%`})</Typography></li>
                        {crsVar()}
                    </ul>
                </Grid>
            </Grid>
        )
    }

    const Content = () => {
        return(
            <Grid container style={{padding: '0px 50px'}}>
                <Grid item xs={12} style={{marginLeft: '10%', marginTop: '25px'}}>
                    <Typography variant="h2"><b>Ranking</b></Typography>
                    <Typography variant="caption">(Większy wynik oznacza lepszy)</Typography>
                </Grid>
                <Grid item xs={12} style={{marginTop: '30px'}}>
                    <ResponsiveContainer width={'100%'} height={300}>
                        <BarChart data={rank}>
                            <CartesianGrid strokeDasharray="5 5" />
                            <XAxis dataKey="Nazwa" />
                            <YAxis type="number"/>
                            <Tooltip cursor={false}/>
                            <Bar barSize={180} radius={[5,5,0,0]} dataKey="Waga wariantu" fill="#eda034" legendType='circle' background='false'/>
                        </BarChart>
                    </ResponsiveContainer>
                </Grid>
                <Grid item xs={6} style={{marginLeft: '20%', marginTop: '15px'}}>
                    <RankComponent />
                </Grid>

                <Grid item xs={12} style={{marginLeft: '10%', marginTop: '25px'}}>
                    <Typography variant="h2"><b>Współczynniki spójności</b></Typography>
                    <Typography variant="caption">({`CR>0.1 oznacza niekonsekwentność w decyzjach`})</Typography>
                </Grid>
                <Grid item xs={12}>
                    <ConsistencyRatiosData />
                </Grid>

                <Grid item xs={12} align="center">
                    <Grid container justify="space-evenly" style={{margin: '30px 0'}}>
                        <Button variant="contained" color="primary" style={{marginRight: '20px'}} onClick={() => {props.addExpertData([data, props.currentExpertName]); props.cb(); props.resetCriteriaSliders(); props.resetVariantSliders()}}>Zapisz wyniki</Button>
                        <Button variant="outlined" onClick={exportJSON}>Eksport JSON</Button>
                    </Grid> 
                </Grid>
            </Grid>
        
        )
    }

    return(
        <div>
            <Paper>
                {data === undefined ? '' : <Content />}
            </Paper>
        </div>
    )
}

const mapStateToProps = state => {
    return state;
}

export default connect(mapStateToProps, { addExpertData, resetCriteriaSliders, resetVariantSliders })(Ranking);