import React, { useState } from 'react';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AppBar from '@material-ui/core/AppBar';
import Container from '@material-ui/core/Container';
import theme from '../theme';
import ThemeProvider from '@material-ui/styles/ThemeProvider';
import TabPanel from './TabPanel';
import Settings from '@material-ui/icons/Settings';
import Typography from '@material-ui/core/Typography';
import FormatListNumberedIcon from '@material-ui/icons/FormatListNumbered';
import FormatListNumberedRtlIcon from '@material-ui/icons/FormatListNumberedRtl';
import EqualizerIcon from '@material-ui/icons/Equalizer';
import PeopleOutlineIcon from '@material-ui/icons/PeopleOutline';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Slider from '@material-ui/core/Slider';
import { connect } from 'react-redux';
import './App.css';

import Homepage from './Homepage';
import CriteriasSliders from './CriteriasSliders';
import VariantsSliders from './VariantsSliders';
import Ranking from './Ranking';
import Experts from './Experts';

const App = ( { currentExpert }) => {
    const [value, setValue] = useState(0);
    const [buttonsState, setButtonsState] = useState([0,1,1,1,1]);
    
    const toggleButtons = (...btnIds) => {
        const newBtnState = buttonsState.map((btnId,index) => btnIds.includes(index) ? btnId^=1 : btnId);
        setButtonsState(newBtnState);
    }

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const switchTabPanelToCriterias = () => {
        setValue(1);
    }

    const switchTabPanelToExperts = () => {
        setValue(4);
    }

    return(
        <ThemeProvider theme={theme}>
            <Container fixed>
                <AppBar position="static">
                    <Tabs value={value} onChange={handleChange} variant="fullWidth">      
                        <Tab disabled={Boolean(buttonsState[0])} icon={<Settings />} label="start"/>
                        <Tab disabled={Boolean(buttonsState[1])} icon={<FormatListNumberedIcon />} label="kryteria"/>
                        <Tab disabled={Boolean(buttonsState[2])} icon={<FormatListNumberedRtlIcon />} label="warianty"/>
                        <Tab disabled={Boolean(buttonsState[3])} icon={<EqualizerIcon />} label="ranking"/>
                        <Tab disabled={Boolean(buttonsState[4])} icon={<PeopleOutlineIcon />} label="decydenci"/>
                        <Tab disabled label={
                            <div style={{opacity: '50%'}}>
                                aktywny ekspert:<br />
                                {currentExpert}
                            </div>
                        }/>
                    </Tabs>
                </AppBar>

                <TabPanel value={value} index={0}>
                    <Homepage tglBtn={toggleButtons} switchPanel={switchTabPanelToCriterias}/>
                </TabPanel>

                <TabPanel value={value} index={1}>
                    <CriteriasSliders />
                </TabPanel>

                <TabPanel value={value} index={2}>
                    <VariantsSliders />
                </TabPanel>

                <TabPanel value={value} index={3}>
                    <Ranking tglBtn={toggleButtons} cb={switchTabPanelToExperts}/>
                </TabPanel>

                <TabPanel value={value} index={4}>
                    <Paper>
                        <Experts tglBtn={toggleButtons} cb={switchTabPanelToCriterias}/>
                    </Paper>
                </TabPanel>
            </Container>
        </ThemeProvider>
    )
}

const mapStateToProps = state => {
    return{
        currentExpert: state.currentExpertName
    }
}

export default connect(mapStateToProps)(App);