import React, { useState } from 'react';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import RemoveIcon from '@material-ui/icons/Remove';
import AddIcon from '@material-ui/icons/Add';

import { connect } from 'react-redux';
import { addCriterion, addVariant, deleteCriterion, deleteVariant } from '../../actions';
import '../App.css';

const ItemList = ( { items, type, addCriterion, addVariant, deleteCriterion, deleteVariant  } ) => {
    const [text, setText] = useState('');

    const handleAddClickButton = () => {
        if(text !== ''){
            if(type === 'kryteria'){
                addCriterion(text);
            }
            else{
                addVariant(text);
            }
        }

        setText('');
    }

    const handleTextChange = (e) => {
        setText(e.target.value);
    }

    const handleDeleteItemButton = (item) => {
        if(type === 'kryteria'){
            deleteCriterion(item);
        }
        else{
            deleteVariant(item);
        }
    }

    const itemLabels = items.map((item) => {
        return(
            <div key={item}>
                <ListItem button>
                    <ListItemIcon>
                        <RemoveIcon onClick={() => {handleDeleteItemButton(item)}}/>
                    </ListItemIcon>
                    <ListItemText primary={item} />
                </ListItem>
            </div>
        )
    })

    return(
        <div>
            <Paper className="paper-div">
                <div className="center-div">
                    <TextField value={text} onChange={handleTextChange} id="outlined-basic" label={`Wprowadź ${type === 'kryteria' ? 'kryterium' : 'wariant'}`} variant="outlined" />
                    <AddIcon fontSize="large" className="icon" onClick={handleAddClickButton}/>
                </div>
                <List>
                    {itemLabels}
                </List>
            </Paper>
        </div>
    )
}

const mapStateToProps = (state, ownProps) => {
    //ownProps.type === 'kryteria' ? state.criterias : []

    if(ownProps.type === 'kryteria'){
        return {
            items: state.criterias
        }
    }
    else{
        return {
            items: state.variants
        }
    }
}

export default connect(mapStateToProps, {addCriterion, addVariant, deleteCriterion, deleteVariant})(ItemList);