import React, { useState } from 'react';
import Slider from '@material-ui/core/Slider';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import { updateCriterionSlider, updateVariantSlider } from '../actions';
import makeStyles from '@material-ui/styles/makeStyles';
import './App.css';

const useStyles = makeStyles({
    root: {
        color: 'rgba(15, 35, 75, 1)',
        opacity: '10%',
        '&:hover': {
            opacity: '100%'
        }
      },
    thumb: {
        transition: 'color 0.3s ease-out, height 0.2s ease-out, width 0.2s ease-out',
        '&:hover': {
            color: 'rgba(45, 85, 155, 1)',
            width: 14,
            height: 14,
        }
    },
    mark: {
        display: 'none'
    },
    rail: {
    },
    track: {
    },
});

const theme = createMuiTheme({
    direction: 'rtl',
});

const MultiRangeSlider = ( { leftComp, rightComp, sliderValue, updateCriterionSlider, updateVariantSlider, whichCriteria } ) => {
    const [value, setValue] = useState(sliderValue);
    const classes = useStyles();

    const marks = [
        {value: -7, label: rightComp},
        {value: 9, label: leftComp}
    ]

    const handleSliderChange = (event, newValue) => {
        setValue(newValue);

        if(whichCriteria === undefined){
            updateCriterionSlider(leftComp, rightComp, value);
        }else{
            updateVariantSlider(leftComp, rightComp, whichCriteria, value);
        }
    }

    return(
        <ThemeProvider theme={theme}>
            <div dir="rtl" className = "slider">
                <Slider
                    value={value}
                    onChange={handleSliderChange}
                    marks={marks}
                    step={0.01}
                    min={-7}
                    max={9}
                    style={{width: '50%', marginBottom: '50px'}}
                    classes={{
                        root: classes.root,
                        thumb: classes.thumb,
                        rail: classes.rail,
                        mark: classes.mark,
                        track: classes.track
                    }}
                />
            </div>
        </ThemeProvider>
    )
}

const mapStateToProps = (state, ownProps) => {
    if(ownProps.whichCriteria === undefined){
        for(let i=0; i<state.criteriasSliders.length; i++){
            if(state.criteriasSliders[i].leftComp === ownProps.leftComp && state.criteriasSliders[i].rightComp === ownProps.rightComp){
                return {
                    sliderValue: state.criteriasSliders[i].value
                }
            }
        }
    }
    else{
        for(let i=0; i<state.variantsSliders.length; i++){
            if(state.variantsSliders[i].leftComp === ownProps.leftComp && state.variantsSliders[i].rightComp === ownProps.rightComp && state.variantsSliders[i].whichCriteria === ownProps.whichCriteria){
                return {
                    sliderValue: state.variantsSliders[i].value
                }
            }
        }
    }
}

export default connect(mapStateToProps, { updateCriterionSlider, updateVariantSlider })(MultiRangeSlider);