import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { Paper, Grid, Typography } from '@material-ui/core';

import MultiRangeSlider from './MultiRangeSlider';

const CriteriasSliders = ( { sliders } ) => {
    const [group, setGroup] = React.useState([]);

    useEffect(() => {
        const sliderGroup = sliders.map((slider, i) => {
            return(
                <Grid item xs={12} style={{textAlign:'center', order: Math.floor(Math.random() * 10) + 1}} key={i}>
                    <MultiRangeSlider 
                        leftComp = {slider.leftComp}
                        rightComp = {slider.rightComp}
                        value = {slider.value}
                    />
                </Grid>
            )
        })

        setGroup(sliderGroup);
    }, []);

    return(
        <Paper>
            <Grid container justify="center" style={{display: 'flex', justifyContent: 'center'}}>
                    <Typography variant="h5" style={{margin: '30px 0'}}>
                        Porównaj które kryteria mają większe znaczenie:
                    </Typography>
                    {group}
            </Grid>
        </Paper>
    )
}

const mapStateToProps = state => {
    return {
        sliders: state.criteriasSliders
    };
}

export default connect(mapStateToProps)(CriteriasSliders);