import React from 'react';
import { Grid, Paper , List, ListSubheader, ListItem, ListItemIcon, ListItemText, Collapse, Typography } from '@material-ui/core';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import PersonIcon from '@material-ui/icons/Person';

const SingleExpert = ( { name, ranking, crs }) => {
    const [open, setOpen] = React.useState(true);

    const handleClick = () => {
      setOpen(!open);
    };

    const rankData = () => {
        const keys = Object.keys(ranking);
        const arr = [];

        for(const key of keys){
            arr.push(
                <li>
                    <b>{key}</b> : {(Math.round(ranking[key]*1000)/1000)}
                </li>
            )
        }

        return arr;
    }

    const crsData = () => {
        return crs.map(cr => {
            return(
                <li>
                    {Math.round(cr*1000)/1000}
                </li>
            )
        })
    }

    const expertData = () => {
        return(
            <Grid container>
                <Grid item xs={6}>
                    Finalne wagi:
                    <ul>
                        {rankData()}
                    </ul>
                </Grid>
                <Grid item xs={6}>
                    wsp. spójności:
                    <ul>
                        {crsData()}
                    </ul>
                </Grid>
            </Grid>
        )
    }

    return(
        <List>
          <ListItem button onClick={handleClick}>
              <ListItemIcon>
              <PersonIcon />
              </ListItemIcon>
              <ListItemText primary={name} />
              {open ? <ExpandLess /> : <ExpandMore />}
          </ListItem>
          <Collapse in={open} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
              <ListItem button disabled>
                  <Typography variant="overline">
                      {expertData()}
                  </Typography>
              </ListItem>
              </List>
          </Collapse>
      </List>
    )
}

export default SingleExpert;