export const addCriterion = (criterion) => {
    return{
        type: 'ADD_CRITERION',
        payload: criterion
    }
}

export const addVariant = (variant) => {
    return{
        type: 'ADD_VARIANT',
        payload: variant
    }
}

export const deleteCriterion = (criterion) => {
    return{
        type: 'DELETE_CRITERION',
        payload: criterion
    }
}

export const deleteVariant = (variant) => {
    return{
        type: 'DELETE_VARIANT',
        payload: variant
    }
}

export const addCriterionSlider = (leftComp, rightComp) => {
    return{
        type: 'ADD_CRITERION_SLIDER',
        payload: { leftComp, rightComp }
    }
}

export const updateCriterionSlider = (leftComp, rightComp, value) => {
    return{
        type: 'UPDATE_CRITERION_SLIDER',
        payload: { leftComp, rightComp, value }
    }
}

export const addVariantSlider = (leftComp, rightComp, whichCriteria) => {
    return{
        type: 'ADD_VARIANT_SLIDER',
        payload: { leftComp, rightComp, whichCriteria }
    }
}

export const updateVariantSlider = (leftComp, rightComp, whichCriteria, value) => {
    return{
        type: 'UPDATE_VARIANT_SLIDER',
        payload: { leftComp, rightComp, whichCriteria, value }
    }
}

export const updateExpertName = (expertName) => {
    return{
        type: 'UPDATE_EXPERT_NAME',
        payload: expertName
    }
}

export const resetCriteriaSliders = () => {
    return{
        type: 'RESET_CRITERIA_SLIDERS'
    }
}

export const resetVariantSliders = () => {
    return{
        type: 'RESET_VARIANT_SLIDERS'
    }
}

export const addExpertData = (data) => {
    return{
        type: 'ADD_EXPERT_DATA',
        payload: data
    }
}