import { combineReducers } from "redux";

const criteriasReducer = (state = ['Kryterium1', 'Kryterium2', 'Kryterium3'], action) => {
    if(action.type === 'ADD_CRITERION'){
        return [...state, action.payload]
    }

    else if(action.type === 'DELETE_CRITERION'){
        return [...state].filter(criterion => criterion !== action.payload);
    }

    return state;
}

const variantsReducer = (state = ['Wariant1', 'Wariant2', 'Wariant3'], action) => {
    if(action.type === 'ADD_VARIANT'){
        return [...state, action.payload]
    }

    else if(action.type === 'DELETE_VARIANT'){
        return [...state].filter(variant => variant !== action.payload);
    }

    return state;
}

const criteriasSlidersReducer = (state = [], action) => {
    if(action.type === 'ADD_CRITERION_SLIDER'){
        return [...state, {...action.payload, value: [0,1,2]}];
    }
    else if(action.type === 'UPDATE_CRITERION_SLIDER'){
        let foundIndex;
        
        state.findIndex((item, index) => {
            if(item.leftComp === action.payload.leftComp && item.rightComp === action.payload.rightComp){
                foundIndex = index;
            }
        })

        return [...state].map((obj, index) => { 
            if(index === foundIndex){
                obj.value = action.payload.value;
            }
            return obj;
        });
    }
    else if(action.type === 'RESET_CRITERIA_SLIDERS'){
        let tempState = state.map(obj => Object.assign({}, obj));
        tempState.forEach(obj => {
            obj["value"] = [0,1,2];
        })
        console.log(tempState);
        return tempState;
    }

    return state;
}

const variantsSlidersReducer = (state = [], action) => {
    if(action.type === 'ADD_VARIANT_SLIDER'){
        return [...state, {...action.payload, value: [0,1,2]}];
    }
    else if(action.type === 'UPDATE_VARIANT_SLIDER'){
        let foundIndex;
        
        state.findIndex((item, index) => {
            if(item.leftComp === action.payload.leftComp && item.rightComp === action.payload.rightComp && item.whichCriteria === action.payload.whichCriteria){
                foundIndex = index;
            }
        })

        return [...state].map((obj, index) => { 
            if(index === foundIndex){
                obj.value = action.payload.value;
            }
            return obj;
        });
    }
    else if(action.type === 'RESET_VARIANT_SLIDERS'){
        let tempState = state.map(obj => Object.assign({}, obj));
        tempState.forEach(obj => {
            obj["value"] = [0,1,2];
        })
        console.log(tempState);
        return tempState;
    }

    return state;
}

const expertNameReducer = (state = "EKSPERT 1", action) => {
    if(action.type === 'UPDATE_EXPERT_NAME'){
        return action.payload;
    }
    
    return state;
}

const expertsReducer = (state = [], action) => {
    if(action.type === 'ADD_EXPERT_DATA'){
        const data = action.payload[0];
        const expertName = action.payload[1];

        for(let i=0; i<state.length; i++){
            if(state[i].expertName === expertName){
                alert("Dane decydenta o tym ID znajdują się już w bazie");
                return state;
            }
        }

        return [...state, 
            {
                expertName: expertName,
                crs: [data.criteriasChoiceCr, ...data.alternativesChoicesCrs],
                ranking: data.rank
            }
        ]
    }

    return state;
}

export default combineReducers({
    currentExpertName: expertNameReducer,
    criterias: criteriasReducer,
    variants: variantsReducer,
    criteriasSliders: criteriasSlidersReducer,
    variantsSliders: variantsSlidersReducer,
    expertsData: expertsReducer,
})