import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { Paper, Grid, Typography } from '@material-ui/core';

import MultiRangeSlider from './MultiRangeSlider';

const VariantsSliders = ( { sliders, criterias, variants }) => {
    const [group, setGroup] = React.useState([]);
    const howManyCompares = (variants.length**2 - variants.length)/2

    useEffect(() => {
        const container = criterias.map((criteria, i) => {
            const sliderGroup = [];

            for(let i=0; i<howManyCompares; i++){
                sliderGroup.push(
                    <Grid item xs={12} style={{textAlign:'center', order: Math.floor(Math.random() * 10) + 1}} key={i}>
                        <MultiRangeSlider 
                            leftComp = {sliders[i].leftComp}
                            rightComp = {sliders[i].rightComp}
                            whichCriteria = {criteria}
                            value = {sliders[i].value}
                        />
                    </Grid>
                )
            }

            return(
                <Paper style={{marginBottom: '20px'}} key={i}>
                    <Grid container justify="center" style={{display: 'flex', justifyContent: 'center'}}>
                        <Typography variant="h5" style={{margin: '30px 0'}}>
                            Opierając się o <b>{criteria}</b>, która z alternatyw jest lepsza?
                        </Typography>
                        {sliderGroup}
                    </Grid>
                </Paper>
            )
        })

        setGroup(container);
    }, []);

    return(
        <div>
            {group}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        sliders: state.variantsSliders,
        criterias: state.criterias,
        variants: state.variants
    };
}

export default connect(mapStateToProps)(VariantsSliders);