import React, { useState } from 'react';
import Grid from '@material-ui/core/Grid';
import ItemList from './HomePageComponents/ItemList';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import './App.css';

import { connect } from 'react-redux';
import { addCriterionSlider, addVariantSlider, updateExpertName } from '../actions';

const Homepage = ( { tglBtn, switchPanel, criterias, variants, addCriterionSlider, addVariantSlider, updateExpertName } ) => {
    const [expertId, setExpertId] = useState("EKSPERT 1");

    const isEnoughItems = (criterias.length > 1 && variants.length > 1) ? false : true;
    
    const loadSlidersToState = () => {
        for(let i=0; i<criterias.length; i++){
            for(let j=i+1; j<criterias.length; j++){
                addCriterionSlider(criterias[i], criterias[j]);
            }
        }

        for(let i=0; i<criterias.length; i++){
            for(let j=0; j<variants.length; j++){
                for(let k=j+1; k<variants.length; k++){
                    addVariantSlider(variants[j], variants[k], criterias[i]);
                }
            }
        }
    }

    const handleExpertIdChange = (e) => {
        updateExpertName(e.target.value);
        setExpertId(e.target.value);
    }

    return(
        <Grid container spacing={3}>
            <Grid item xs={5}>
                <ItemList type="kryteria"/>
            </Grid>
            <Grid item xs={5}>
                <ItemList type="warianty"/>
            </Grid>
            <Grid item xs={2}>
                <TextField value={expertId} onChange={handleExpertIdChange} id="filled-basic" label="ID Decydenta" variant="filled" style={{marginBottom: '5px'}}/>
                <Button disabled={isEnoughItems} variant="contained" onClick={() => {tglBtn(0,1,2,3,4);switchPanel(); loadSlidersToState()}} color="primary" size="large" style={{height: '50px', width: '100%'}}>
                    Zatwierdź
                </Button>
            </Grid>
        </Grid>
    )
}

const mapStateToProps = state => {
    return state;
}

export default connect(mapStateToProps, { addCriterionSlider, addVariantSlider, updateExpertName })(Homepage);